import axios from 'axios'; // Axios -    HTTP-.
import throttle from 'lodash.throttle'; // Lodash Throttle -       .
import Notiflix from 'notiflix'; // Notiflix -      -.
import 'notiflix/dist/notiflix-3.2.6.min.css';

import { refs, totalPages } from './refs';
import { createMarkup } from './markup';

const BASE_URL = 'https://pixabay.com/api/';

const API_KEY = '39476118-a69449891a99ee741726132f7';

//  getPhotos  HTTP-  API Pixabay    .
async function getPhotos(userInput, currentPage = 1) {
  //     API Pixabay
  const params = new URLSearchParams({
    key: API_KEY, //  API Pixabay
    q: userInput, //   
    image_type: 'photo', //   ()
    orientation: 'horizontal', //   ()
    safesearch: 'true', //      
    page: currentPage, //   
    per_page: 40, //    
  });

  try {
    //  HTTP GET-  API Pixabay   
    const response = await axios.get(`${BASE_URL}?${params}`);
    const photoArr = response.data;

    if (parseInt(photoArr.totalHits) > 0) {
      //   ,        
      const markup = createMarkup(photoArr.hits);
      refs.gallery.innerHTML += markup;

      // console.log('photoArr.hits', photoArr.hits);
      // console.log('photoArr.totalHits', photoArr.totalHits);

      Notiflix.Notify.info(`Hooray! We found ${photoArr.totalHits} images.`);

      if (photoArr.hits !== photoArr.totalHits) {
        //      ,    "Load more"
        refs.loadMoreButton.classList.remove('hidden');
      }

      if (userInput === '') {
        currentPage = 1;
      }

      //      
      const totalPages = Math.ceil(photoArr.totalHits / 40); // 40 -     

      //     ,      
      return { currentPage, totalPages, hits: photoArr.hits };
    } else {
      Notiflix.Notify.failure(
        ',  ,   . ,   .'
      );
    }

    //   .
    refs.input.value = '';
  } catch (error) {
    //          Notiflix.
    console.error(error);
    Notiflix.Notify.failure(
      ',  ,   . ,   .'
    );
  }
}

export { getPhotos };
